@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");
html {
  box-sizing: border-box;
  font-size: 16px; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-family: "Roboto", sans-serif; }

ol,
ul,
li {
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }

*:focus {
  outline: none; }

